import React from 'react';
import Loading from './loading';
import styled from 'styled-components';
import { convertDate } from '../../utils/helpers';
import VideoPlayer from 'react-simple-video-player';
const Iframe = styled.iframe`
width: 70%;
height:85%;
@media (max-width: 1200px) {
  width: 80%;
  height:70%;
}
@media (max-width: 1000px) {
  width: 80%;
  height:60%;
}
@media (max-width: 769px) {
  width: 100%;
  height:60%;
}

}
`;
const Container = styled.div`
height: 100vh;
@media (max-width: 1000px) {
  height: 90vh;
}
  @media (max-width: 769px) {
    height: 80vh;
  }
  @media (max-width: 450px) {
    height: 60vh;
  }
`;
class VideoEmbed extends React.Component {
  state = { isClicked: false };

  handleClick = () => {
    this.setState({ isClicked: !this.state.isClicked });
  };

  render() {
    if (!this.props.video) {
      return <Loading />;
    }
    if (this.props.video) {
      const { video } = this.props;
      const videoId = video.id.videoId;
      const url = `https://www.youtube.com/embed/${videoId}`;

      const { snippet } = this.props.video;
      return (
        <div>
          <Container>
         
            <div align="center" className="embed-responsive embed-responsive-16by9">
            <iframe width="100%" title="video player" src={url} frameBorder="0" allowFullScreen allowscriptaccess="always" />

            </div>
            <h4>{snippet.title}</h4>
            <h5 style={{ color: 'rgb(150,150,150)' }}>
              Published on: {convertDate(snippet.publishedAt)}
            </h5>
          </Container>

        </div>
      );
    }
  }
}
export default VideoEmbed;
