import React from 'react';
import SimpleVideo from './simple-video';



const Video = ({ videos, onVideoSelect }) => {
  return (
    <div>
      <div className="container">
        <div className="row">
          {videos.map(video => {
            return (
              <SimpleVideo
                key={video.etag}
                video={video}
                onVideoSelect={onVideoSelect}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};
export default Video;
