import React, { Component } from 'react';
import styled from 'styled-components';
import './item.css'
const Container = styled.div`
height: 100vh;
@media (max-width: 1000px) {
  height: 90vh;
}
  @media (max-width: 769px) {
    height: 80vh;
  }
  @media (max-width: 450px) {
    height: 60vh;
  }
`;

class Home extends Component {

    onseclectvideo = () => {
        this.props.onseclectvideo(this.props.item.id);
    }

    render() {
        var { item, index } = this.props;
        let videoId = item.id;
        let url = `http://localhost:8080/video/${videoId}`;
        return (

                <div>
                   
                        <div align="center" >

                            <video key={url} width={900} height={700} controls>
                                <source src={url} type="video/mp4" />
                            </video>

                        </div>
                        <section className="info">
                            <div className="metadata">
                                <div className="titleAndButtonchung">
                                    <span className="title clamp">
                                        {item.videosname.replace(".mp4", "")}
                                    </span>

                                </div>
                                <span className="views">{item.views + 1 + " "}{" Lượt views "} {" "}</span>
                            </div>
                            <ul className="actions">
                                <li>
                                    <button>
                                    <i className="fa fa-heart active activehoverheart" /><span>{item.like === 0 ? 'No one' : '0' + item.like}</span>
                                    </button>
                                </li>
                                <li>
                                    <button><i className="fa fa-thumbs-o-down active" /><span>0</span></button>
                                </li>
                                <li>
                                    <button onClick={this.onshare}><i className="fa fa-share-square-o active" /><span>Share</span></button>
                                </li>
                                <li>
                                    <button onClick={this.saveVideo}  ><i href={`http://localhost:8080/video/${videoId}`} download className="fa fa-floppy-o active" /><span>Save</span></button>
                                </li>



                                <li>
                                    <button>
                                        <i className="fa fa-bug active" aria-hidden="true" /><span>Report</span>
                                    </button>
                                </li>
                            </ul>
                            <div className="channel">
                                <div className="metadata">
                                    <div className="info">
                                    </div>
                                </div>
                            </div>
                        </section>

                </div>


 
        )
    }
}

export default Home;
