import React, { Component } from 'react';
import SearchBar from './videos-youtube/containers/search-bar';
import Video from './videos-youtube/components/video';
import VideoEmbed from './videos-youtube/components/video-embed';
import YTSearch from 'youtube-api-search';
import { API_KEY } from './utils/apiKey';
import '../news/news.css'
import './AppVideo.css'
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      videos: [],
      selectedVideo: null,
      status: true,
      statusViewSubmit: true,
      term: null,
      Typevideo: null,
      checked1: false,
      checked2: false,
      checked3: false,
      checked4: false,
      checked5: false,
      checked6: false,
    };


  }
  componentWillMount() {
    if (localStorage && localStorage.getItem('Typevideo')) {
      var Typevideos = JSON.parse(localStorage.getItem('Typevideo'));
      
      this.setState({
        Typevideo: Typevideos,
        statusViewSubmit:false,
      checked1: Typevideos[0],
      checked2: Typevideos[1],
      checked3: Typevideos[2],
      checked4: Typevideos[3],
      checked5: Typevideos[4],
      checked6: Typevideos[5],
      });
      var term = 'Tin tức';

      if (Typevideos[0]) {term=term+' Thế giới'; }
      if (Typevideos[1]) {term=term+' Kinh tế';  }
      if (Typevideos[2]) { term=term+' Giáo dục';  }
      if (Typevideos[3]) {term=term+' Thể thao'; }
      if (Typevideos[4]) {term=term+' Pháp luật';  }
      if (Typevideos[5]) { term=term+' Khoa học';  }
      
      this.videoSearch(term);
    }
    else
    {
    this.videoSearch('Corona Viet Nam');
  }

  }

  async videoSearch(term) {

    await YTSearch({ key: API_KEY, term }, videos => {
      this.setState({
        videos,
        selectedVideo: videos[0]
      });
      setTimeout(
        this.ytseach2(term), 0);

    });




  }
  ytseach2 = (term) => {
    var term = term + 'và Mỹ';
    var videos1 = this.state.videos;

    let promise = new Promise((resolve, reject) => {
      YTSearch({ key: API_KEY, term }, videos => {

        // videos.forEach(vd => {
        //   videos1.push(vd);      
        // });
        resolve(videos)
      });
    })

    promise.then((videos) => {
		console.log(videos);
      this.setState({
        videos: [...this.state.videos, ...videos]
      });
    })
  }

  onChangeUI = () => {
    this.setState({
      status: !this.state.status
    });
  };
  handleChange1 = () => {
    this.setState({
      checked1: !this.state.checked1
    });
  };

  handleChange2 = () => {
    this.setState({
      checked2: !this.state.checked2
    });
  };
  handleChange3 = () => {
    this.setState({
      checked3: !this.state.checked3
    });
  };
  handleChange4 = () => {
    this.setState({
      checked4: !this.state.checked4
    });
  };
  handleChange5 = () => {
    this.setState({
      checked5: !this.state.checked5
    });
  };
  handleChange6 = () => {
    this.setState({
      checked6: !this.state.checked6
    });
  };
  onSubmitTypeOfVideo = () => {
    var Typevideo = [];
    if (this.state.checked1) { Typevideo.push(true); } else Typevideo.push(false)
    if (this.state.checked2) { Typevideo.push(true); } else Typevideo.push(false);
    if (this.state.checked3) { Typevideo.push(true); } else Typevideo.push(false);
    if (this.state.checked4) { Typevideo.push(true); } else Typevideo.push(false);
    if (this.state.checked5) { Typevideo.push(true); } else Typevideo.push(false);
    if (this.state.checked6) { Typevideo.push(true); } else Typevideo.push(false);
    this.setState({
      Typevideo: Typevideo,
      statusViewSubmit:false
    });

    var term = 'Tin tức';

      if (Typevideo[0]) {term=term+' Thế giới'; }
      if (Typevideo[1]) {term=term+' Kinh tế';  }
      if (Typevideo[2]) { term=term+' Giáo dục';  }
      if (Typevideo[3]) {term=term+' Thể thao'; }
      if (Typevideo[4]) {term=term+' Pháp luật';  }
      if (Typevideo[5]) { term=term+' Khoa học';  }
      this.videoSearch(term);
      console.log(term);
    localStorage.setItem('Typevideo', JSON.stringify(Typevideo));

  }
  onChangeTopicWatch=()=>
  {
    this.setState({
      statusViewSubmit:true
    })
  }
  render() {

    //var FormUser=this.state.Typevideo===null?"":"";
    var elementViewRenderFormTypeVideo=this.state.statusViewSubmit===true?        <div className="container">
    <div className="row text-center">
      <br />
      <br />
      <h1>Chọn chủ đề</h1>
      <h2></h2>
      <br />
      <label htmlFor="default" className="btn btn-default editsizeform">Thế giới<input type="checkbox" id="default" className="badgebox" checked={this.state.checked1} onChange={this.handleChange1} /><span className="badge">✓</span></label>
      <label htmlFor="primary" className="btn btn-default  editsizeform">Kinh tế<input type="checkbox" id="primary" className="badgebox" checked={this.state.checked2} onChange={this.handleChange2} /><span className="badge">✓</span></label>
      <label htmlFor="info" className="btn btn-default editsizeform">Giáo dục<input type="checkbox" id="info" className="badgebox" checked={this.state.checked3} onChange={this.handleChange3} /><span className="badge">✓</span></label>
      <label htmlFor="success" className="btn btn-default editsizeform">Thể Thao<input type="checkbox" id="success" className="badgebox" checked={this.state.checked4} onChange={this.handleChange4} /><span className="badge">✓</span></label>
      <label htmlFor="warning" className="btn btn-default editsizeform">Pháp luật<input type="checkbox" id="warning" className="badgebox" checked={this.state.checked5} onChange={this.handleChange5} /><span className="badge">✓</span></label>
      <label htmlFor="danger" className="btn btn-default editsizeform">Khoa học<input type="checkbox" id="danger" className="badgebox" checked={this.state.checked6} onChange={this.handleChange6} /><span className="badge">✓</span></label>

    </div>
    <button className="block12" onClick={this.onSubmitTypeOfVideo}>Subimt</button>
  </div>:"";
    var enlementRender = this.state.status === true ? <div >
      <SearchBar onSearch={term => this.videoSearch(term)} />

      <VideoEmbed video={this.state.selectedVideo} />
      <Video
        videos={this.state.videos}
        onVideoSelect={selectedVideo => this.setState({ selectedVideo })}
      />
    </div> : <div>
        <SearchBar onSearch={term => this.videoSearch(term)} />
        {this.state.videos.map(video => {
          return (
            <VideoEmbed video={video} />
          );
        })}

      </div>;
      var changeTopicWatch=this.state.statusViewSubmit===false?<button className="block12" onClick={this.onChangeTopicWatch}>Thay đổi chủ đề xem</button>:""

    return (
      <div>

        {elementViewRenderFormTypeVideo}
        <div className="row">
          <div className="tm-section-header section-margin-top">
            <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
    <div className="col-lg-4 col-md-6 col-sm-6"><h2 className="tm-section-title">Videos</h2>  <button className="block12" onClick={this.onChangeUI}>Thay đổi giao diện</button>{changeTopicWatch}</div>
            <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
          </div>
        </div>
        {enlementRender}

      </div>
    );
  }

}
export default App;
