import React from 'react';





const SimpleVideo = ({ video, onVideoSelect }) => {
  const image = video.snippet.thumbnails.high.url;
  const title = video.snippet.title;
  return (

    <div onClick={() => onVideoSelect(video)}>
    <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6 col-xxs-12" >
      <div className="tm-tours-box-2 exam1" >
        <img src={image} alt={title} height='150px' width='250px' />
        <div className="tm-tours-box-2-info">
          <h3 className="margin-bottom-15">{title}</h3>
        </div>
        <div onClick={() => onVideoSelect(video)}>
        <a className="tm-tours-box-2-link editviewtag1 ">Xem video</a>
      </div>
      </div>
    </div>
    </div>
  
  );
};
export default SimpleVideo;




